// @import "node_modules/bootstrap-sass/assets/stylesheets/bootstrap";

html, body {
  height: 100%;
  font-family: 'Lato'; }

.errorTitle {
  text-align: center;
  font-size: 100px; }

/* Navigation Style */
nav {
  position: relative;
  z-index: 99; }

.brand-logo:hover {
  text-decoration: none; }

.fnt10 {
  font-size: 12px;
}

.loginError, .approval {
  display: none;
}

.btn-flat {
  text-transform: none !important;
}

.row .col {
  padding: 0!important;
}

.fntWhite {
  color: #fff;
}
input[type=text]:focus:not([readonly]), input[type=password]:focus:not([readonly]), input[type=email]:focus:not([readonly]), input[type=url]:focus:not([readonly]), input[type=time]:focus:not([readonly]), input[type=date]:focus:not([readonly]), input[type=datetime-local]:focus:not([readonly]), input[type=tel]:focus:not([readonly]), input[type=number]:focus:not([readonly]), input[type=search]:focus:not([readonly]), textarea.materialize-textarea:focus:not([readonly]) {
  border-bottom: 1px solid #009688 !important ;
  box-shadow: 0 1px 0 0 #009688 !important;
}
.tSLink {
  font-size: 150%; }

.welNavBk {
  border-bottom: 2px solid transparent; }

.welNavBk:hover, .welNavBk.active {
  border-bottom: 2px solid #1abc9c;
  text-decoration: none;
  color: #fff;
  background-color: #00877B; }

.loginBlade, .regBlade {
  margin-top: 13%; }

.profileImg img {
  width: 50px;
  height: 50px;
  border-radius: 100%;
  display: inline-block; }

.fa-btn {
  margin-right: 6px; }

.navigationBlk {
  background: rgba(255, 255, 255, 0.9); }

.slide img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0; }

select {
  display: block;
}

.drpDown {
  position: relative!important;
  top: -60px!important;
}

/* landing page */
.lpTxtBlk {
  font-family: 'Lato', sans-serif;
  font-weight: 300;
  position: relative;
  top: 100px;
  z-index: 2;
  color: #000;
  font-size: 400%; }

.mainTxt {
  margin-bottom: 20px;
  text-align: center; }

.lpTxtBlk span {
  color: #3498db; }

.subTxt {
  font-size: 32%; }

.has-error .error {
  color: #a94442;
  margin-top: 0; }

#registerframe {
  position: relative;
  z-index: 9999; }

@media screen and (max-width: 968px) {
  .slide img {
    height: auto; } }
